"use client";

import React, { useState } from 'react';
import { Sidebar } from '@/components/layout/Sidebar';
import { Navbar } from '@/components/layout/Navbar';
import { ConnectionSelector } from '@/components/dashboard/ConnectionSelector';
import { ChatInterface } from '@/components/dashboard/ChatInterface';
import { MetricCard } from '@/components/dashboard/MetricCard';
import { ChartPanel } from '@/components/dashboard/ChartPanel';
import { DataTable } from '@/components/dashboard/DataTable';
import { LayoutDashboard, Users, DollarSign, ShoppingCart, Calendar } from 'lucide-react';
import { motion } from 'framer-motion';
import { Card } from '@/components/ui/Card';

export default function DashboardPage() {
    const [hasSearched, setHasSearched] = useState(false);
    const [isThinking, setIsThinking] = useState(false);

    const handleSearch = (query: string) => {
        setIsThinking(true);
        // Mock API delay
        setTimeout(() => {
            setIsThinking(false);
            setHasSearched(true);
        }, 2000);
    };

    return (
        <div className="min-h-screen bg-gray-50 flex flex-col font-sans">
            <Navbar />

            <div className="flex pt-16 h-screen overflow-hidden">
                {/* Sidebar */}
                <Sidebar />

                {/* Main Content */}
                <main className="flex-1 ml-64 p-8 overflow-y-auto pb-24 scroll-smooth">
                    <div className="max-w-7xl mx-auto space-y-8">

                        {/* Top Bar: Title & Connection Selector */}
                        <div className="flex items-center justify-between">
                            <div>
                                <h1 className="text-2xl font-bold text-gray-900 tracking-tight">Dashboard</h1>
                                <p className="text-sm text-gray-500 mt-1">Welcome back, User</p>
                            </div>
                            <ConnectionSelector />
                        </div>

                        {/* Chat Interface */}
                        <div className="py-4">
                            <ChatInterface onSearch={handleSearch} isThinking={isThinking} />
                        </div>

                        {/* Results Section - Conditionally Rendered */}
                        {hasSearched && (
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.5, ease: "easeOut" }}
                                className="space-y-8"
                            >
                                {/* Metrics Row */}
                                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                                    <MetricCard
                                        label="Total Revenue"
                                        value="$34,200"
                                        icon={DollarSign}
                                        trend="up"
                                        trendValue="12%"
                                        color="indigo"
                                    />
                                    <MetricCard
                                        label="Active Users"
                                        value="1,240"
                                        icon={Users}
                                        trend="up"
                                        trendValue="4%"
                                        color="blue"
                                    />
                                    <MetricCard
                                        label="Orders"
                                        value="856"
                                        icon={ShoppingCart}
                                        trend="neutral"
                                        trendValue="0% "
                                        color="emerald"
                                    />
                                    <MetricCard
                                        label="Date Range"
                                        value="Last 30 Days"
                                        icon={Calendar}
                                        color="violet"
                                        subValue="Jan 1 - Jan 30"
                                    />
                                </div>

                                {/* Chart and Table Layout */}
                                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                                    <div className="lg:col-span-2 h-[450px]">
                                        <ChartPanel />
                                    </div>
                                    <div className="lg:col-span-1 h-[450px]">
                                        {/* Using ChartPanel with different default or Pie chart for variety could go here, 
                               but let's put another chart or a summary for now. 
                               Actually user asked for Chart panel, Table, Metrics. 
                               Let's put table below charts for full width.
                           */}
                                        <Card className="h-full border-0 shadow-lg shadow-gray-100 flex flex-col justify-center items-center text-center p-6">
                                            <div className="bg-indigo-50 p-4 rounded-full text-indigo-500 mb-4">
                                                <LayoutDashboard size={32} />
                                            </div>
                                            <h3 className="font-semibold text-gray-900">AI Summary</h3>
                                            <p className="text-sm text-gray-500 mt-2 max-w-[200px]">
                                                Sales have increased by 15% compared to last month. Electronics is the top performing category.
                                            </p>
                                        </Card>
                                    </div>
                                </div>

                                <div className="w-full">
                                    <DataTable />
                                </div>
                            </motion.div>
                        )}

                        {!hasSearched && !isThinking && (
                            <div className="flex flex-col items-center justify-center py-20 text-gray-400">
                                <LayoutDashboard size={48} className="mb-4 text-gray-200" />
                                <p className="text-lg font-medium">No query results yet</p>
                                <p className="text-sm">Ask a question above to see data visualization.</p>
                            </div>
                        )}

                    </div>
                </main>
            </div>
        </div>
    );
}
