import React, { useState } from 'react';
import { ChevronLeft, ChevronRight, Search, Download } from 'lucide-react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { cn } from '@/utils/cn';

const mockData = [
    { id: 1, product: 'Wireless Mouse', category: 'Electronics', price: 29.99, stock: 150, status: 'In Stock' },
    { id: 2, product: 'Mechanical Keyboard', category: 'Electronics', price: 129.99, stock: 45, status: 'Low Stock' },
    { id: 3, product: 'Ergonomic Chair', category: 'Furniture', price: 349.99, stock: 12, status: 'Low Stock' },
    { id: 4, product: 'USB-C Cable', category: 'Accessories', price: 19.99, stock: 500, status: 'In Stock' },
    { id: 5, product: '27" Monitor', category: 'Electronics', price: 399.99, stock: 25, status: 'In Stock' },
];

export const DataTable = () => {
    const [currentPage, setCurrentPage] = useState(1);
    const [searchTerm, setSearchTerm] = useState('');

    // Filter data mock
    const filteredData = mockData.filter(item =>
        item.product.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.category.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <Card className="border-0 shadow-lg shadow-gray-100 overflow-hidden">
            <CardHeader className="flex flex-row items-center justify-between pb-4">
                <CardTitle className="text-lg font-semibold text-gray-800">Query Results</CardTitle>
                <div className="flex items-center gap-2">
                    <div className="relative w-64">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={16} />
                        <input
                            type="text"
                            placeholder="Search results..."
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="w-full pl-9 pr-4 py-2 text-sm border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500/20 focus:border-indigo-500 transition-all"
                        />
                    </div>
                    <Button variant="outline" size="sm" className="gap-2">
                        <Download size={16} />
                        Export
                    </Button>
                </div>
            </CardHeader>
            <div className="overflow-x-auto">
                <table className="w-full text-sm text-left">
                    <thead className="bg-gray-50 border-b border-gray-100 text-gray-500 uppercase text-xs font-semibold">
                        <tr>
                            <th className="px-6 py-3">Product Name</th>
                            <th className="px-6 py-3">Category</th>
                            <th className="px-6 py-3">Price</th>
                            <th className="px-6 py-3">Stock</th>
                            <th className="px-6 py-3">Status</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100">
                        {filteredData.map((row) => (
                            <tr key={row.id} className="hover:bg-gray-50/50 transition-colors">
                                <td className="px-6 py-4 font-medium text-gray-900">{row.product}</td>
                                <td className="px-6 py-4 text-gray-600">{row.category}</td>
                                <td className="px-6 py-4 text-gray-600">${row.price}</td>
                                <td className="px-6 py-4 text-gray-600">{row.stock}</td>
                                <td className="px-6 py-4">
                                    <span className={cn(
                                        "px-2 py-1 rounded-full text-xs font-medium",
                                        row.status === 'In Stock' ? "bg-green-100 text-green-700" : "bg-yellow-100 text-yellow-700"
                                    )}>
                                        {row.status}
                                    </span>
                                </td>
                            </tr>
                        ))}
                        {filteredData.length === 0 && (
                            <tr>
                                <td colSpan={5} className="px-6 py-8 text-center text-gray-400">
                                    No results found.
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
            <div className="flex items-center justify-between px-6 py-4 bg-gray-50/50 border-t border-gray-100">
                <span className="text-sm text-gray-500">Showing 1 to {filteredData.length} of {mockData.length} entries</span>
                <div className="flex gap-2">
                    <Button variant="outline" size="sm" disabled className="h-8 w-8 p-0">
                        <ChevronLeft size={16} />
                    </Button>
                    <Button variant="outline" size="sm" className="h-8 w-8 p-0 bg-white shadow-sm border-indigo-200 text-indigo-600">
                        1
                    </Button>
                    <Button variant="outline" size="sm" disabled className="h-8 w-8 p-0">
                        <ChevronRight size={16} />
                    </Button>
                </div>
            </div>
        </Card>
    );
};
