"use client";

import React, { useState } from 'react';
import {
    BarChart as RechartsBarChart,
    Bar,
    LineChart as RechartsLineChart,
    Line,
    PieChart as RechartsPieChart,
    Pie,
    Cell,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer
} from 'recharts';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { BarChart3, LineChart, PieChart } from 'lucide-react';
import { cn } from '@/utils/cn';

// Mock Data
const data = [
    { name: 'Jan', sales: 4000, profit: 2400, amt: 2400 },
    { name: 'Feb', sales: 3000, profit: 1398, amt: 2210 },
    { name: 'Mar', sales: 2000, profit: 9800, amt: 2290 },
    { name: 'Apr', sales: 2780, profit: 3908, amt: 2000 },
    { name: 'May', sales: 1890, profit: 4800, amt: 2181 },
    { name: 'Jun', sales: 2390, profit: 3800, amt: 2500 },
];

const pieData = [
    { name: 'Electronics', value: 400 },
    { name: 'Clothing', value: 300 },
    { name: 'Home', value: 300 },
    { name: 'Books', value: 200 },
];

const COLORS = ['#4f46e5', '#0ea5e9', '#10b981', '#8b5cf6'];

type ChartType = 'bar' | 'line' | 'pie';

export const ChartPanel = () => {
    const [chartType, setChartType] = useState<ChartType>('bar');

    return (
        <Card className="h-full border-0 shadow-lg shadow-gray-100">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-lg font-semibold text-gray-800">Sales Overview</CardTitle>
                <div className="flex bg-gray-100 p-1 rounded-lg">
                    <button
                        onClick={() => setChartType('bar')}
                        className={cn("p-1.5 rounded-md transition-all", chartType === 'bar' ? "bg-white shadow text-indigo-600" : "text-gray-500 hover:text-gray-700")}
                    >
                        <BarChart3 size={16} />
                    </button>
                    <button
                        onClick={() => setChartType('line')}
                        className={cn("p-1.5 rounded-md transition-all", chartType === 'line' ? "bg-white shadow text-indigo-600" : "text-gray-500 hover:text-gray-700")}
                    >
                        <LineChart size={16} />
                    </button>
                    <button
                        onClick={() => setChartType('pie')}
                        className={cn("p-1.5 rounded-md transition-all", chartType === 'pie' ? "bg-white shadow text-indigo-600" : "text-gray-500 hover:text-gray-700")}
                    >
                        <PieChart size={16} />
                    </button>
                </div>
            </CardHeader>
            <CardContent className="h-[300px] w-full pt-4">
                <ResponsiveContainer width="100%" height="100%">
                    {chartType === 'bar' ? (
                        <RechartsBarChart data={data}>
                            <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#f3f4f6" />
                            <XAxis dataKey="name" axisLine={false} tickLine={false} tick={{ fill: '#6b7280', fontSize: 12 }} />
                            <YAxis axisLine={false} tickLine={false} tick={{ fill: '#6b7280', fontSize: 12 }} />
                            <Tooltip
                                cursor={{ fill: '#f9fafb' }}
                                contentStyle={{ borderRadius: '8px', border: 'none', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                            />
                            <Legend iconType="circle" />
                            <Bar dataKey="sales" fill="#4f46e5" radius={[4, 4, 0, 0]} maxBarSize={50} />
                            <Bar dataKey="profit" fill="#0ea5e9" radius={[4, 4, 0, 0]} maxBarSize={50} />
                        </RechartsBarChart>
                    ) : chartType === 'line' ? (
                        <RechartsLineChart data={data}>
                            <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#f3f4f6" />
                            <XAxis dataKey="name" axisLine={false} tickLine={false} tick={{ fill: '#6b7280', fontSize: 12 }} />
                            <YAxis axisLine={false} tickLine={false} tick={{ fill: '#6b7280', fontSize: 12 }} />
                            <Tooltip
                                contentStyle={{ borderRadius: '8px', border: 'none', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                            />
                            <Legend iconType="circle" />
                            <Line type="monotone" dataKey="sales" stroke="#4f46e5" strokeWidth={3} dot={{ r: 4, fill: '#4f46e5', strokeWidth: 2, stroke: '#fff' }} activeDot={{ r: 6 }} />
                            <Line type="monotone" dataKey="profit" stroke="#0ea5e9" strokeWidth={3} dot={{ r: 4, fill: '#0ea5e9', strokeWidth: 2, stroke: '#fff' }} />
                        </RechartsLineChart>
                    ) : (
                        <RechartsPieChart>
                            <Pie
                                data={pieData}
                                cx="50%"
                                cy="50%"
                                innerRadius={60}
                                outerRadius={80}
                                fill="#8884d8"
                                paddingAngle={5}
                                dataKey="value"
                            >
                                {pieData.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                                ))}
                            </Pie>
                            <Tooltip />
                            <Legend iconType="circle" layout="vertical" align="right" verticalAlign="middle" />
                        </RechartsPieChart>
                    )}
                </ResponsiveContainer>
            </CardContent>
        </Card>
    );
};
